export function getCurrentCityName() {
    if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
            const latitude = position.coords.latitude;
            const longitude = position.coords.longitude;

            // Gi API ca bt k dch v no bn mun s dng vi latitude v longitude  ly tn tnh/thnh ph
            // V d: s dng API ca OpenCage Geocoding
            const apiKey = 'afad171e6912486cbff380bd9ebf33f0';
            const apiUrl = `https://api.opencagedata.com/geocode/v1/json?q=${latitude}+${longitude}&key=${apiKey}`;
            fetch(apiUrl)
                .then((response) => response.json())
                .then((data) => {
                    const city = data.results[0].components.city; // Ly tn thnh ph t d liu tr v
                    // console.log(city);
                    return city;
                })
                .catch((error) => {
                    console.log('Error:', error);
                });
        });
    } else {
        console.log('Geolocation is not supported by this browser.');
    }
}
