export function getCurrentTimeFormat() {
    const date = new Date();
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const meridian = hours >= 12 ? 'PM' : 'AM';

    const formattedHours = hours % 12 || 12;

    const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
    const dayOfWeek = days[date.getDay()];

    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    const month = months[date.getMonth()];

    const currentDate = date.getDate();
    const currentYear = date.getFullYear();

    // ly ra thi gian dng ===> "9:34 pm, Fri, Jan 26, 2024"
    const all = `${formattedHours}:${
        minutes < 10 ? '0' : ''
    }${minutes} ${meridian}, ${dayOfWeek}, ${month} ${currentDate}, ${currentYear}`;

    // ly ra thi gian dng ===> "Jan 26"
    const month_date = `${month} ${currentDate}`;
    let formattedTime = {
        all: all,
        month_date: month_date,
    };
    return formattedTime;
}

export function formatLocalTime(time) {
    if (time) {
        const inputDate = new Date(time);
        const options = { weekday: 'short', month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit' };
        const formattedDate = inputDate.toLocaleString('en-US', { ...options, hour12: true });

        return formattedDate;
    }
}

// chuyn sang ch khng du v vit thng
export function convertToLowerCaseAndRemoveAccents(str) {
    if (!str || typeof str !== 'string' || str.trim() === '') {
        return ''; // Tr v chui trng nu str khng hp l
    }

    const lowerCase = str.toLowerCase(); // Chuyn i thnh ch thng
    const normalizedString = lowerCase.normalize('NFD').replace(/[\u0300-\u036f]/g, ''); // Loi b du

    return normalizedString;
}
