import React from 'react';
import { Line, Chart } from 'react-chartjs-2';
import { Chart as ChartJS } from 'chart.js/auto';
import { useDispatch, useSelector } from 'react-redux';
import { getForecast24h } from '../../../store/ForecastSlice';

const TemperatureChart = () => {
    let dispatch = useDispatch();
    let labels = [];
    let degree = [];
    const { forecastData } = useSelector((state) => state.forecast);
    if (forecastData?.forecast?.forecastday) {
        forecastData.forecast.forecastday.forEach((day) => {
            day.hour.forEach((item) => {
                labels.push(item.time.split(' ')[1]);
                degree.push(item.temp_f);
            });
        });
    }

    let minDegree = Math.min(...degree) - 10;
    let maxDegree = Math.max(...degree) + 10;

    const currentDate = new Date();

    // t s pht v giy ca currentDate thnh 0
    currentDate.setMinutes(0);
    currentDate.setSeconds(0);

    // Ly ra s gi ca currentDate
    const currentHour = currentDate.getHours().toString().padStart(2, '0');

    const currentTimeIndex = labels.indexOf(currentHour + ':00');

    // t pointStyle cho thi im hin ti
    const pointStyles = degree.map((_, index) => {
        return index === currentTimeIndex ? 'circle' : false;
    });

    const data = {
        labels: labels,
        datasets: [
            {
                label: 'Temperature',
                data: degree, // D liu nhit  theo ngy
                borderColor: '#3e95cd',
                backgroundColor: '#EEF4FE',
                fill: true,
                borderWidth: 2,
                pointStyle: pointStyles,
                cubicInterpolationMode: 'monotone',
                pointBackgroundColor: ['blue'],
            },
        ],
    };

    const options = {
        legend: {
            display: false,
            position: 'bottom',
        },
        plugins: {
            legend: {
                display: true,
                align: 'start',
                labels: {
                    boxWidth: 0, // t  rng ca  vung l 0  n n i
                    font: {
                        size: 13, // Kch thc ch
                    },
                    color: '#626161',
                },
            },
            tooltip: {
                callbacks: {
                    title: function (tooltipItems, data) {
                        // Thay i tiu  ca tooltip  y
                        return '';
                    },
                    label: function (tooltipItem, data) {
                        const index = tooltipItem.dataIndex;

                        // Kim tra xem ch s c ph hp vi ch s ca thi im hin ti khng
                        if (index === currentTimeIndex) {
                            // Tr v nhit  hin ti km vi n v F
                            return degree[index] + ' F';
                        } else {
                            // Tr v nhit  ca cc im khc
                            return degree[index] + ' F';
                        }
                    },
                },
            },
        },
        scales: {
            x: {
                display: false,
                grid: {
                    display: false, // Loi b cc ng k  trn trc x
                },
            },
            y: {
                // beginAtZero: true,
                min: minDegree,
                max: maxDegree,
                display: false,
                grid: {
                    display: false, // Loi b cc ng k  trn trc y
                },
            },
        },
    };

    return (
        <div className="w-100 d-flex">
            <Line data={data} options={options} />
        </div>
    );
};

export default TemperatureChart;
